@media (min-width: 765px) and (max-width: 1366px) {

    // Header
    .header__title {
        font-size: smoot(57, 48, 1366, 765);
    }

    .header__desc {
        font-size: smoot(18, 14, 1366, 765);
    }

    .btn__text {
        font-size: smoot(14, 12, 1366, 765);
    }

    .nav {
        font-size: smoot(16, 13.5, 1366, 765);
    }

    // Main
    .title-section {
        font-size: smoot(48, 36, 1366, 765);
    }

    .desc-section {
        font-size: smoot(16, 13.5, 1366, 765);
    }
}



@media (min-width: 765px) and (max-width: 900px) {

    //Header
    .header__desc {
        margin-top: 0.5rem;
        margin-bottom: 1rem;
    }

    .btn {
        padding: 0 15px;
        line-height: 1.5rem;

        &__arrow {
            display: none;
        }
    }

    // Main
    .row-base__content .row-base__blockImg .row-base__img1 {
        left: -2rem;
    }
}




@media screen and (max-width: 764px) {
    // Header
    .container {
        padding: 0 rem(60px);
    }

    .header {
        background-image: url(../img/images/bgheaderplanshet.png);
        background-position: top -7px left -4px;
        background-size: 103.2% auto;
        padding-top: rem(30px);
    }

    .row-header {
        background-image: url(../img/images/fotoplanshet.png);
        background-position: top 39px right -120px;
        background-size: 98.2% auto;
    }

    .header__title {
        font-size: 3rem;
    }

    .header__desc {
        font-size: 14px;
    }

    .header__topLine {
        margin-bottom: 5.6rem;
    }

    .btn {
        &__arrow {
            display: none;
        }
    }

    .nav {
        z-index: 100;
        text-align: center;
    }

    .nav__link {
        font-size: 13px;
        font-weight: lighter;
    }

    // Main
    .row-base{
        background-image: none;        
    }

    .title-section {
        font-size: 1.5rem;
    }

    .desc-section {
        max-width: 19.750rem;
        font-size: 1rem;
    }

    .row-base__content .row-base__blockImg .row-base__img1,
    .row-base__content .row-base__blockImg .row-base__img2{
      display: none;
    }
}



@media (min-width: 321px) and (max-width: 764px) {
    .header__title {
        font-size: smoot(48, 18, 764, 321);
    }

    .header__desc {
        font-size: smoot(14, 10, 764, 321);
    }

    .btn__text {
        font-size: smoot(14, 8, 764, 321);
    }
}

@media screen and (max-width: 654px) {
    .container {
        padding: 0 rem(30px);
    }

    .btn {
        padding: 0 10px;
        line-height: 2rem;
    }
}

@media screen and (max-width: 587px) {
    .container {
        padding: 0 rem(15px);
    }

    .row-header {
        background-position: top 39px right rem(-75px);
    }

    .header__topLine {
        margin-bottom: 1.5rem;
    }

    .phone {
        visibility: hidden;
    }

    .burger {
        margin-left: 3rem;
    }

    .header__title {
        margin-bottom: 1.9rem;
    }

    .header__desc {
        display: none;
        margin-top: 0.2rem;
        margin-bottom: 0.4rem;
    }

    .nav {
        z-index: 100;
        width: 50%;
        top: 110%;
        left: 0;
        text-align: center;

        &__link {
            background-color: $fc3;
            font-size: 1rem;
            border-bottom: 1px solid $fc8;
            border-right: 1px solid $fc8;
        }

    }
}

@media screen and (max-width: 320px) {
    .header {
        background-image: url(../img/images/bgheadermob.png);
        background-position: top -55px left -168px;
        background-size: 167% auto;
        padding-top: rem(15px);
    }

    .row-header {
        background-image: url(../img/images/fotomob.png);
        background-position: top 120px right -56px;
        background-size: 98.2% auto;
    }

    .header__topLine {
        padding-right: 0;
        margin-right: -0.3rem;
        margin-bottom: rem(75px);
    }

    .logo {
        background-image: url(../img/images/logomob.png);
        height: rem(31px);
        width: rem(38px);
    }

    .burger {
        @include gamburger (16px, 2px, 3px, $fc1, $fc3);

        &__line {
            &:first-child {
                width: rem(10px);
            }
        }
    }

    .header__title {
        font-size: 1.875rem;
        font-weight: 100;
        margin-bottom: rem(25px);
        letter-spacing: 0.094rem;
    }

    .header__desc {
        display: none;
    }

    .phone {
        display: none;
    }

    .btn__text {
        font-size: 0.688rem;
        font-weight: 500;
    }

    .btn {
        padding: 0 rem(16px);
        line-height: 2rem;
    }

    .nav {
        z-index: 100;
        width: 100%;
        top: 120%;
        left: 0;
        text-align: center;

        &__link {
            padding: rem(5px) 0;
            font-size: rem(20px);
            border-left: 1px solid $fc8;
        }
    }
}